.wrapper {
  width: 100px;
  height: 70px;
  background-color: white;
  border-radius: 20px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  margin-left: 35px;
  line-height: 70px;
  text-align: center;
  &:hover {
    background-color: var(--color-active-sidebar);
    cursor: pointer;
    transition-duration: 0.3s;
  }
}
